.overlay {
  background-color: rgb(2, 2, 2, 0.6);
  backdrop-filter: blur(3px);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 208%;
  z-index: 11;
  animation: slide-down 300ms ease-out forwards;
}

.cart_items {
  position: fixed;
  top: -2.5rem;
  width: 70%;
  height: 5rem;
  margin-left: 15%;
  z-index: 12;
  transition: all 0.6s;
  animation: slide-down 300ms ease-out forwards;
}
.order_cart {
  position: fixed;
  top: -1rem;
  margin-left: 15%;
  z-index: 12;
  height: 3rem;
  width: 70%;
  transition: all 0.6s;
  animation: slide-down 300ms ease-out forwards;
}

@media (max-width: 893px) {
  .cart_items {
    top: -4rem;
    width: 130%;
    margin-left: -15%;
  }
}
@media (max-width: 736px) {
  .cart_items {
    top: -3.5rem;
    width: 130%;
    margin-left: -15%;
  }
}
@media (max-width: 634px) {
  .cart_items {
    top: -2.5rem;
    width: 130%;
    margin-left: -15%;
  }
}
@media (max-width: 464px) {
  .cart_items {
    top: -1.5rem;
    width: 130%;
    margin-left: -15%;
  }
}

@media (max-width: 1146px) {
  .order_cart {
    top: 0.5rem;
    width: 140%;
    margin: -10% -20%;
  }
}

@keyframes slide-down {
  from {
    opacity: 0;
    transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
